body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: white;
}

.bg-color {
  background-color: #2196f3;
}

.user-img-container {
  background-color: #2196f3;
}

#profile-image {
  margin: 60px auto 0 auto;
  padding: 2px;
  height: 160px;
  width: 200px;
  border-radius: 50%;
}

#header {
  height: 800px;
  background-color: #2196f3;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  line-height: 500px;
}

#headerDiv {
  text-align: center;
  color: white;
}
.App {
  text-align: center;
  background-image: url('/images/oli.jpg');
  position: relative;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 1000px;
  width: 100%;
}

.App-header {
  height: 150px;
  padding: 20px;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

/* item card styles */
#cardDiv {
    bottom: 100px;
    align-items: flex-start;
}

.project-content {
    display: inline-block;
    top: 0;
    height: 475px;
    width: 350px;
    margin: 15px;
    padding: 20px;
    line-height: 0;
}

.projects-div {
    justify-content: center;
}

/* card image */ 
#img {
    height: 250px;
    width: 350px;
    border: 2px solid grey;
}

#card{
    padding: 15px;
}

.link-image {
    height: 350px;
    width: 265px;
}

.project-link-button {
    color: white;
    align-content: space-between;
    
}

.about-image {
    height: 325px;
    width: 450px;
}

#hit-the-floor {
    color: #fff;
    font-size: 7em;
    font-weight: bold;
    font-family: Helvetica;
    text-shadow: 0 1px 0 #ccc, 0 2px 0 white, 0 3px 0 white, 0 4px 0 #b9b9b9, 0 5px 0 #aaa, 0 6px 1px rgba(0,0,0,.1), 0 0 5px rgba(0,0,0,.1), 0 1px 3px rgba(0,0,0,.3), 0 3px 5px rgba(0,0,0,.2), 0 5px 10px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.2), 0 20px 20px rgba(0,0,0,.15);
    }
#formInput {
    text-align: center;
    display: 'flex';
    margin: 20px;
}

h1 {
    font-size: 64px;
    text-align: center;
}

h3 {
    padding: 15px;
    margin: 15px;
}

#formMenu {
    display: inline;
    padding: 25px;
    margin: 40px;
}

#projectTable {
    padding: 30px;
    margin: 15px;
}

#mainDiv {
    margin: flex;
    height: -webkit-fill-available;
    height: -moz-available;
    height: stretch;
    /* padding: 300px; */
    background: white;
}
